<template>
  <div
    v-loading="isLoading"
    class="create-template"
  >
    <h3
      v-if="!isView"
      class="create-title"
    >{{ createType !== 1 ? '新建模板' : '修改模板' }}</h3>
    <gs-form
      ref="form"
      :model="formData"
      :rules="rules"
      label-width="90px"
    >
      <gs-form-item
        label="模板名称"
        prop="templateName"
        required
      >
        <span v-if="isView">{{ formData.templateName }}</span>
        <gs-input
          v-else
          v-model="formData.templateName"
          placeholder="请输入模板名称"
        />
      </gs-form-item>

      <gs-form-item
        label="生成周期"
        prop="buildType"
        required
      >
        <span v-if="isView">{{ formData.buildType === 0 ? `周期生成(${formData.periodCron})` : '一次性生成' }}</span>
        <gs-radio-group
          v-else
          v-model="formData.buildType"
          @change="buildTypeChange"
        >
          <gs-radio :label="1">生成一次</gs-radio>
          <gs-radio :label="0">周期生成</gs-radio>
        </gs-radio-group>
      </gs-form-item>

      <gs-form-item
        v-if="formData.buildType === 0 && !isView"
        label=""
        prop="periodCron"
      >
        <crontab
          v-model="formData.periodCron"
          :active="formData.active"
        />
      </gs-form-item>
      <gs-tabs
        v-model="activeTab"
        type="content-tab"
      >
        <gs-tab-pane
          label="表规则"
          name="tableRules"
        >
          <gs-form-item
            v-loading="isLoadingRules && !isLoading"
            class="field-table-item relative"
            label="表规则"
            prop="tableRules"
          >
            <create-template-table
              v-model="formData.tableRules"
              :table-rules="tableRules"
              :is-view="isView"
            />
          </gs-form-item>
        </gs-tab-pane>
        <gs-tab-pane
          label="字段规则"
          name="columnsRules"
        >
          <gs-form-item
            v-loading="isLoadingRules && !isLoading"
            class="field-table-item relative"
            label="字段规则"
            prop="columnsRules"
          >
            <create-template-field
              v-model="formData.columnsRules"
              :columns-rules="columnsRules"
              :is-view="isView"
            />
          </gs-form-item>
        </gs-tab-pane>
      </gs-tabs>
      <!-- <gs-form-item
        v-loading="isLoadingRules && !isLoading"
        class="field-table-item relative"
        label="表规则"
        prop="tableRules"
      >
        <create-template-table
          v-model="formData.tableRules"
          :table-rules="tableRules"
          :is-view="isView"
        />
      </gs-form-item>

      <gs-form-item
        v-loading="isLoadingRules && !isLoading"
        class="field-table-item relative"
        label="字段规则"
        prop="columnsRules"
      >
        <create-template-field
          v-model="formData.columnsRules"
          :columns-rules="columnsRules"
          :is-view="isView"
        />
      </gs-form-item> -->

    </gs-form>
    <div class="action-btns">
      <gs-button
        v-if="isView"
        slot="reference"
        @click="$emit('back')"
      >返回</gs-button>
      <template v-else>
        <gs-button
          :loading="isLoading"
          type="primary"
          @click="create"
        >确定</gs-button>
        <gs-button @click="cancelCreate">取消</gs-button>
      </template>
    </div>
  </div>
</template>

<script>
import './create-template.scss';
import ActionComfirm from 'src/components/action-comfirm';
import CreateTemplateTable from './create-template-table';
import CreateTemplateField from './create-template-field';
import Crontab from 'src/components/crontab/crontab';
import * as dataQualityService from 'src/service/metadata-management/data-quality';
export default {
  components: {
    Crontab,
    CreateTemplateTable,
    CreateTemplateField,
    ActionComfirm
  },

  props: {
    templateId: {
      type: String,
      default: ''
    },
    createType: {
      type: Number,
      default: 0
    },
    buildType: {
      type: Number,
      default: 1
    }
  },

  data() {
    return {
      isLoading: false,
      isLoadingRules: false,
      formData: {
        templateName: '',
        buildType: this.buildType,
        active: 'hour',
        periodCron: '',
        tableRules: [],
        columnsRules: []
      },
      tableRules: [],
      columnsRules: [],
      source: '',
      rules: {
        templateName: [
          {
            validator: (rule, value, cb) => {
              if (!value) {
                cb(new Error('模板名称不能为空'));
              } else {
                cb();
              }
            }
          }
        ],
        tableRules: [{ validator: this.validatorTableRules }],
        columnsRules: [{ validator: this.validatorColumnsRules }]
      },
      activeTab: 'tableRules'
    };
  },
  computed: {
    isView() {
      return this.createType === 3;
    }
  },

  beforeMount() {
    if (this.createType === 0) {
      this.getRules();
    } else {
      this.isLoading = true;
      Promise.all([this.getTemplateDetail(), this.getRules()])
        .then((res) => {
          this.isLoading = false;
          this.initData(res[0]);
        })
        .catch(() => {
          this.isLoading = false;
        });
    }
  },
  methods: {
    /**
     * 初始化模板详情数据
     */
    initData(templateDetail) {
      this.formData.tableRules = [];
      this.formData.columnsRules = [];

      let tableRulesMap = {};
      this.tableRules.forEach(item => {
        tableRulesMap[item.ruleName] = item;
      });
      let columnsRulesMap = {};
      this.columnsRules.forEach(item => {
        item.columnRules.forEach(i => {
          columnsRulesMap[i.ruleId] = i;
        });
      });
      const tableRules = [];
      const columnsRules = [];
      templateDetail.rules.forEach(item => {
        // 判断规则类型为表规则还是字段规则
        if (item.ruleType === 0) {
          tableRules.push({
            selectRule: tableRulesMap[item.ruleName],
            ruleCheckType: item.ruleCheckType,
            ruleCheckExpression: item.ruleCheckExpression,
            ruleCheckThreshold: item.ruleCheckThreshold
          });
        } else {
          columnsRules.push({
            ruleColumnName: item.ruleColumnName,
            selectRule: columnsRulesMap[item.systemRuleId],
            ruleCheckType: item.ruleCheckType,
            ruleCheckExpression: item.ruleCheckExpression,
            ruleCheckThreshold: item.ruleCheckThreshold,
            selectColumn: this.columnsRules.find(i => i.ruleColumnName === item.ruleColumnName)
          });
        }
      });
      this.formData.tableRules = tableRules;
      this.formData.columnsRules = columnsRules;
      this.formData.templateName = this.createType === 2 ? templateDetail.templateName + '_copy' : templateDetail.templateName;
      this.formData.buildType = templateDetail.buildType;
      this.formData.periodCron = templateDetail.periodCron;
    },
    create() {
      const form = this.$refs.form;
      form.validate((isValid) => {
        if (isValid) {
          this.$Modal.confirm({
            title: (this.createType !== 1 ? '新建' : '修改') + '模板',
            content: '确定' + (this.createType !== 1 ? '新建' : '修改') + '数据质量规则模板吗？',
            onOk: () => {
              if (this.createType !== 1) {
                this.addTemplate();
              } else {
                this.updateTemplate();
              }
            },
            onCancel: () => {}
          });
        } else {
          this.$Message.warning(`请检查输入`);
        }
      });
    },
    /**
     * 添加模板
     */
    addTemplate() {
      this.isLoading = true;
      dataQualityService.addTemplate(this.getSavedata()).then((res) => {
        this.isLoading = false;
        this.$Message.success('创建模板成功，正在跳转到模板列表页...', 1.5);
        setTimeout(() => {
          this.$emit('back', true, this.formData.templateName);
        }, 1500);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 修改模板
     */
    updateTemplate() {
      this.isLoading = true;
      dataQualityService.updateTemplate(this.templateId, this.getSavedata()).then((res) => {
        this.isLoading = false;
        this.$Message.success('修改模板成功，正在跳转到模板列表页...', 1.5);
        setTimeout(() => {
          this.$emit('back', true);
        }, 1500);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 获取保存数据
     */
    getSavedata() {
      let rules = [];
      this.formData.tableRules.forEach(item => {
        rules.push({
          systemRuleId: item.selectRule.ruleId,
          ruleType: 0,
          ruleName: item.selectRule.ruleName,
          ruleInfo: item.selectRule.ruleInfo,
          ruleCheckType: item.ruleCheckType,
          ruleCheckExpression: item.ruleCheckExpression,
          ruleCheckThreshold: item.ruleCheckThreshold,
          ruleCheckThresholdUnit: item.selectRule.ruleCheckThresholdUnit
        });
      });
      this.formData.columnsRules.forEach(item => {
        rules.push({
          systemRuleId: item.selectRule.ruleId,
          ruleType: 1,
          ruleName: item.selectRule.ruleName,
          ruleInfo: item.selectRule.ruleInfo,
          ruleColumnName: item.selectColumn.ruleColumnName,
          ruleColumnType: item.selectColumn.ruleColumnType,
          ruleCheckType: item.ruleCheckType,
          ruleCheckExpression: item.ruleCheckExpression,
          ruleCheckThreshold: item.ruleCheckThreshold,
          ruleCheckThresholdUnit: item.selectRule.ruleCheckThresholdUnit
        });
      });
      return {
        tableId: this.$route.params.id,
        templateName: this.formData.templateName,
        buildType: this.formData.buildType,
        periodCron: this.formData.buildType === 1 ? '' : this.formData.periodCron,
        rules,
        source: this.source
      };
    },
    /**
     * 生成周期change事件
     */
    buildTypeChange(value) {
      this.$nextTick(() => {
        this.formData.buildType = this.formData.buildType === 0 ? 1 : 0;
        if ((this.formData.tableRules && this.formData.tableRules.length) ||
          (this.formData.columnsRules && this.formData.columnsRules.length)) {
          this.$Modal.confirm({
            title: `改变生成周期将清空表和字段规则，确定要改变吗？`,
            onOk: () => {
              this.formData.buildType = this.formData.buildType === 0 ? 1 : 0;
              this.formData.tableRules = [];
              this.formData.columnsRules = [];
              if (value === 1) {
                this.formData.periodCron = '';
              }
              this.getRules();
            }
          });
        } else {
          this.formData.buildType = this.formData.buildType === 0 ? 1 : 0;
          if (value === 1) {
            this.formData.periodCron = '';
          }
          this.getRules();
        }
      });
    },
    /**
     * 根据生成方式获取规则
     */
    getRules() {
      this.isLoadingRules = true;
      return dataQualityService.getRules(this.$route.params.id, this.formData.buildType).then((res) => {
        this.isLoadingRules = false;
        const setRuleCheckTypeOptions = item => {
          let checkTypeOptions = [];
          item.ruleAllowCheckType.forEach(ele => {
            checkTypeOptions.push({
              label: ele === 0 ? '固定值' : '波动值',
              value: ele
            });
          });
          item.ruleCheckTypeOptions = checkTypeOptions;
        };
        res.tableRules.forEach(setRuleCheckTypeOptions);
        res.columnsRules.forEach(item => {
          item.columnRules.forEach(setRuleCheckTypeOptions);
        });
        this.tableRules = res.tableRules;
        this.columnsRules = res.columnsRules;
        this.source = res.source;
      }).catch((res) => {
        this.isLoadingRules = false;
      });
    },
    /**
     * 查看模板详情
     */
    getTemplateDetail() {
      return dataQualityService.getTemplateDetail(this.templateId);
    },

    cancelCreate() {
      this.$Modal.confirm({
        title: '确定取消' + (this.createType !== 1 ? '新建' : '修改') + '数据质量规则模板吗？',
        onOk: () => {
          this.$emit('back');
        }
      });
    },
    validatorTableRules(rule, value, cb) {
      if ((this.formData.columnsRules && this.formData.columnsRules.length) && (value && !value.length)) {
        cb();
      }
      if (!value || (value && !value.length)) {
        cb(new Error('表规则不能为空'));
      } else {
        const ruleIds = value.map(v => v.selectRule.ruleId);
        const uniqueIds = Array.from(new Set(ruleIds));
        if (ruleIds.length === uniqueIds.length) {
          const required = value.every(item => {
            if (item.ruleCheckType) {
              return !Number.isNaN(Number(item.ruleCheckThreshold[0])) && !Number.isNaN(Number(item.ruleCheckThreshold[0]));
            } else {
              return !Number.isNaN(Number(item.ruleCheckThreshold[0]));
            }
          });
          required ? cb() : cb(new Error('请填写阈值大小'));
        } else {
          cb(new Error('表规则不能重复'));
        }
      }
    },
    validatorColumnsRules(rule, value, cb) {
      if ((this.formData.tableRules && this.formData.tableRules.length) && (value && !value.length)) {
        cb();
      }
      if (!value || (value && !value.length)) {
        cb(new Error('字段规则不能为空'));
      } else {
        const ruleIds = value.map(v => v.selectColumn.ruleColumnName + v.selectRule.ruleId);
        const uniqueIds = Array.from(new Set(ruleIds));
        if (ruleIds.length === uniqueIds.length) {
          const required = value.every(item => {
            if (item.ruleCheckType) {
              return !Number.isNaN(Number(item.ruleCheckThreshold[0])) && !Number.isNaN(Number(item.ruleCheckThreshold[0]));
            } else {
              return !Number.isNaN(Number(item.ruleCheckThreshold[0]));
            }
          });
          required ? cb() : cb(new Error('请填写阈值大小'));
        } else {
          cb(new Error('字段规则不能重复'));
        }
      }
    }
  }
};
</script>




