<template>
  <div class="tabBody">
    <gs-row>
      <gs-radio
        v-model="year.cronEvery"
        label="1"
      >每年</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="year.cronEvery"
        label="2"
      >
        从第 <el-input-number
          v-model="year.incrementStart"
          :min="2018"
          :max="2118"
          size="mini"
        /> 开始，
        每隔 <el-input-number
          v-model="year.incrementIncrement"
          :min="1"
          :max="99"
          size="mini"
        /> 年执行
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="year.cronEvery"
        class="long"
        label="3"
      >具体年份(可多选)
        <el-select
          v-model="year.specificSpecific"
          size="mini"
          filterable
          multiple
        >
          <el-option
            v-for="(val, index) in 100"
            :key="index"
            :label="2017 + val"
            :value="2017 + val"
          />
        </el-select>
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="year.cronEvery"
        label="4"
      >从
        <el-input-number
          v-model="year.rangeStart"
          :min="2018"
          :max="2118"
          size="mini"
        />
        到
        <el-input-number
          v-model="year.rangeEnd"
          :min="2018"
          :max="2118"
          size="mini"
        />
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>
import crontabMixin from './mixins/cron.mixin.js';

export default {
  mixins: [crontabMixin],
  data() {
    return {
      year: {
        cronEvery: '',
        incrementStart: new Date().getFullYear(),
        incrementIncrement: 1,
        rangeStart: 0,
        rangeEnd: 0,
        specificSpecific: []
      }
    };
  },
  computed: {
    text() {
      let years = '';
      let cronEvery = this.year.cronEvery;
      switch (cronEvery.toString()) {
        case '1':
          years = '*';
          break;
        case '2':
          years = this.year.incrementStart + '/' + this.year.incrementIncrement;
          break;
        case '3':
          this.year.specificSpecific.map(val => {
            years += val + ',';
          });
          years = years.slice(0, -1);
          break;
        case '4':
          years = this.year.rangeStart + '-' + this.year.rangeEnd;
          break;
        default:
          break;
      }
      return years;
    }
  }
};
</script>
<style></style>



