<template>
  <div
    class="metadata-management-data-quality relative"
  >
    <div v-if="!showCreate">
      <gs-button
        class="add-btn"
        type="primary"
        @click="toCreateTemplate(0)"
      >新建质量规则</gs-button>
      <div>
        <div class="left relative">
          <Template-tree
            ref="sideTree"
            :loading="isLoading"
            :active="activeTreeNode"
            :default-expanded-keys="defaultExpandedKeys"
            :tree-props="treeData"
            node-key="value"
            @node-click="handleSelectTreeNode"
            @alert-click="handleAlertClick"
            @copyTemplate="toCopyTemplate"
            @checkReportDetail="checkReportDetail"
          />
        </div>
        <div class="right relative">
          <template-detail
            v-if="activeOverview"
            :template-info="activeTreeNode"
            @editTemplate="toEditTemplate"
            @copyTemplate="toCopyTemplate"
            @viewTemplate="toViewTemplate"
            @handleDelete="handleDelete"
            @handleChangeStatus="handleChangeStatus"
          />
          <report-list
            v-if="activeOverview"
            :template-id="activeTreeNode.templateId"
            :template-status="activeTreeNode.templateStatus"
            @checkReportDetail="checkReportDetail"
          />
          <report-detail
            v-if="!activeOverview"
            :report-id="reportId"
            @backToOverview="backToOverview"
          />
          <div
            v-if="!activeTreeNode.type"
            class="no-content"
          />
        </div>
      </div>
    </div>
    <create-template
      v-if="showCreate"
      :template-id="activeTreeNode.templateId"
      :create-type="createType"
      :build-type="buildType"
      @back="back"
    />
  </div>
</template>

<script>
import './data-quality.scss';
import TemplateDetail from './template-detail.vue';
import ReportDetail from './report-detail.vue';
import TemplateTree from './template-tree.vue';
import ReportList from './report-list.vue';
import CreateTemplate from './create-template.vue';
import * as dataQualityService from 'src/service/metadata-management/data-quality';
export default {
  components: {
    TemplateDetail,
    ReportDetail,
    TemplateTree,
    ReportList,
    CreateTemplate
  },
  data() {
    return {
      activeTreeNode: {},
      showCreate: false,
      createType: 0, // 0 新建 1修改 2克隆 3查看
      defaultExpandedKeys: [], // tree默认展开key数组
      treeData: {
        data: [
          {
            name: '周期生成',
            children: []
          },
          {
            name: '一次性生成',
            children: []
          }
        ],
        props: {
          label: 'name',
          value: 'value'
        }
      },
      isLoading: false,
      buildType: 1,
      reportId: '',
      activeOverview: true
    };
  },
  beforeMount() {
    this.getTemplates();
  },
  methods: {
    /**
     * 选中某个tree节点处理方法
     * @param {*} node tree的元素节点
     */
    handleSelectTreeNode(node) {
      if (node === this.activeTreeNode || this.activeTreeNode.isLoading) {
        return;
      }
      this.activeTreeNode = node;
      this.activeOverview = true;
      // if (this.activeTreeNode.type === 1 && !this.activeTreeNode.children) {
      //   this.getNewestReport();
      // }
    },
    /**
     * 处理删除模板操作
     */
    handleDelete() {
      this.getTemplates();
    },
    /**
     * 修改模板状态
     */
    handleChangeStatus() {
      // this.activeTreeNode.templateStatus = this.activeTreeNode.templateStatus === 1 ? 2 : 1;
      this.getTemplates();
    },
    /**
     * 从新建模板返回
     */
    back(refresh, name) {
      this.showCreate = false;
      this.createType = 0;
      if (refresh) {
        this.getTemplates(name);
      }
    },
    /**
     * 获取一个模板的最新报表
     */
    getNewestReport() {
      this.activeTreeNode.isLoading = true;
      dataQualityService.getNewestReport(this.activeTreeNode.value).then(res => {
        this.activeTreeNode.isLoading = false;
        res.type = 2;
        res.name = res.reportName;
        res.value = res.reportId;
        res.templateId = this.activeTreeNode.value;
        this.activeTreeNode.children = [
          res,
          {
            name: '所有报告',
            value: this.activeTreeNode.value + 3,
            templateId: this.activeTreeNode.value,
            type: 3
          }
        ];
        this.treeData.data = [...this.treeData.data];
      }).catch((res) => {
        this.activeTreeNode.isLoading = false;
      });
    },
    /**
     * 到创建模板界面
     */
    toEditTemplate(buildType) {
      this.createType = 1;
      this.buildType = buildType;
      this.showCreate = true;
    },
    toCopyTemplate(buildType) {
      this.createType = 2;
      this.buildType = buildType;
      this.showCreate = true;
    },
    toCreateTemplate(type) {
      this.createType = type;
      this.showCreate = true;
    },
    toViewTemplate(buildType) {
      this.createType = 3;
      this.buildType = buildType;
      this.showCreate = true;
    },
    /**
     * 获取当前表所有模板
     */
    getTemplates(name = '') {
      this.isLoading = true;
      dataQualityService.getTemplates(this.$route.params.id).then((res) => {
        this.isLoading = false;
        this.handleTemplates(res, name);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 处理所有模板，为tree赋值
     */
    handleTemplates(data, name) {
      const singleTemplates = [];
      const cycleTemplates = [];
      data.forEach(item => {
        item.type = 1;
        item.name = item.templateName;
        item.value = item.templateId;
        // if (item.reportId) {
        //   item.children = [
        //     {
        //       type: 2,
        //       name: item.reportName,
        //       value: item.reportId,
        //       templateId: item.templateId,
        //       redAlerts: item.redAlerts,
        //       alert: item.alert,
        //       orangeAlerts: item.orangeAlerts,
        //       reportId: item.reportId
        //     },
        //     {
        //       name: '所有报告',
        //       value: 'all',
        //       templateId: item.templateId,
        //       type: 3
        //     }
        //   ];
        // }
        item.buildType === 0 ? cycleTemplates.push(item) : singleTemplates.push(item);
      });
      this.treeData.data = [
        {
          name: '周期执行规则',
          value: '-1',
          enableClick: false,
          children: cycleTemplates
        },
        {
          name: '单次执行规则',
          value: '-2',
          enableClick: false,
          children: singleTemplates
        }
      ];
      let temp = {};
      if (Object.keys(this.activeTreeNode).length) {
        if (name) {
          temp = [...cycleTemplates, ...singleTemplates].find(item => item.templateName === name);
        } else {
          temp = [...cycleTemplates, ...singleTemplates].find(item => item.templateId === this.activeTreeNode.templateId);
          if (!temp) temp = cycleTemplates.length > 0 ? cycleTemplates[0] : (singleTemplates.length > 0 ? singleTemplates[0] : {});
        }
      } else {
        temp = cycleTemplates.length > 0 ? cycleTemplates[0] : (singleTemplates.length > 0 ? singleTemplates[0] : {});
      }
      this.handleSelectTreeNode(temp);
      this.defaultExpandedKeys = this.activeTreeNode.value ? [cycleTemplates.length > 0 ? '-1' : '-2'] : [];
    },
    handleAlertClick() {
      this.getTemplates();
    },
    checkReportDetail(reportId) {
      if (reportId) {
        this.reportId = reportId;
      } else if (this.activeTreeNode && this.activeTreeNode.reportId) {
        this.reportId = this.activeTreeNode.reportId;
      } else {
        return false;
      }
      this.activeOverview = false;
    },
    backToOverview() {
      this.activeOverview = true;
    }
  }
};
</script>




