<template>
  <div class="tabBody">
    <!-- <gs-row>
      <gs-radio
        v-model="type"
        label="every"
      >每小时</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        label="increment"
      >
        从第 <el-input-number
          v-model="start"
          :min="0"
          :max="23"
          size="mini"
        /> 小时开始，
        每隔 <el-input-number
          v-model="increment"
          :min="0"
          :max="23"
          size="mini"
        /> 小时执行
      </gs-radio>
    </gs-row> -->
    <gs-row>
      <gs-radio
        v-model="type"
        class="long"
        label="specific"
      >具体小时数
        <el-select
          v-model="specific"
          size="mini"
          popper-class="crontab-hour-select"
        >
          <el-option
            v-for="(val, index) in 24"
            :key="index"
            :value="val - 1 + ''"
          >{{ val-1 }}</el-option>
        </el-select>
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>

export default {
  name: 'CrontabHour',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      type: 'specific',
      specific: 0
    };
  },
  computed: {
    cron() {
      let cron = '';
      switch (this.type) {
        case 'every':
          cron = '*';
          break;
        case 'increment':
          cron = `${this.start}/${this.increment}`;
          break;
        case 'specific':
          cron = this.specific;
          break;
        case 'week':
          cron = '?';
          break;
        default:
          break;
      }
      return cron;
    }
  },
  watch: {
    cron(val) {
      this.$emit('input', val);
    },
    value(val) {
      this.resolve(val);
    }
  },
  created() {
    this.resolve(this.value);
  },
  methods: {
    resolve(cron) {
      this.specific = cron;
    }
  }
};
</script>



