<template>
  <div class="metadata-management-detail">
    <div class="content-header">
      <bread-crumb
        :current-level="'表详情'"
        :upper-level="isFromTechnical ? '技术数据' : '元数据管理'"
        :back-path="isFromTechnical ? '/main/data?back' : '/main/metadata?back'"
      />
    </div>
    <div class="content-body">
      <div class="table-name">
        <span
          v-if="!tableInfo.showTableName"
          class="show-visable"
        >-</span>
        <span v-if="!isHdfsPath">{{ tableInfo.showTableName }}</span>
        <span v-if="tableInfo.status === 'DELETED'">(已删除)</span>
        <!-- <a
          @click="refreshTableInfo"
        >
          <i
            :class="{'animation-rotate': isLoading}"
            class="iconfont icon-shuaxin2"
          />
        </a> -->
        <gs-button
          v-show="tableInfo.showTableName && tableInfo.editTag"
          class="add-tag"
          type="primary"
          icon="edit"
          size="small"
          @click="showModal"
        >标签</gs-button>
      </div>
      <gs-tabs
        v-model="activeName"
        type="content-tab"
      >
        <template v-if="!isHdfsPath">
          <gs-tab-pane
            label="表信息"
            name="table"
          >
            <table-info
              v-loading="isLoading"
              v-if="activeName === 'table'"
              :table-info="tableInfo"
              @refreshTableInfo="getTableInfo"
            />
          </gs-tab-pane>
          <gs-tab-pane
            label="字段信息"
            name="field"
          >
            <field-info
              v-if="activeName === 'field'"
              :table-info="tableInfo"
            />
          </gs-tab-pane>
          <gs-tab-pane
            v-if="tableInfo.tablePermission
              && tableInfo.tablePermission.read
              && !tableInfo.virtualTable
            && tableInfo.status !== 'DELETED'"
            :disabled="isLoading"
            label="数据概览"
            name="overview"
          >
            <overview v-if="activeName === 'overview'" />
          </gs-tab-pane>
          <gs-tab-pane
            label="数据血缘"
            name="relationship"
          >
            <relationship
              v-if="activeName === 'relationship'"
              @refreshTableInfo="getTableInfo"
            />
          </gs-tab-pane>
          <gs-tab-pane
            v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
            :disabled="isLoading"
            label="统计信息"
            name="statistics"
          >
            <statistics
              v-if="activeName === 'statistics'"
              @refreshTableInfo="getTableInfo"
            />
          </gs-tab-pane>
          <gs-tab-pane
            v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
            :disabled="isLoading"
            label="数据质量"
            name="data-quality"
          >
            <data-quality
              v-if="activeName === 'data-quality'"
              @refreshTableInfo="getTableInfo"
            />
          </gs-tab-pane>
          <gs-tab-pane
            v-if="tableInfo.tablePermission
              && tableInfo.tablePermission.read
              && !tableInfo.virtualTable
            && tableInfo.status !== 'DELETED'"
            :disabled="isLoading"
            label="建表语句"
            name="tableSql"
          >
            <table-sql v-if="activeName === 'tableSql'" />
          </gs-tab-pane>
        </template>
        <template v-else>
          <gs-tab-pane
            label="数据血缘"
            name="relationship"
          >
            <relationship
              v-if="activeName === 'relationship'"
              @refreshTableInfo="getTableInfo"
            />
          </gs-tab-pane>
        </template>
      </gs-tabs>
    </div>
    <gs-modal
      v-model="visible"
      :width="610"
      class="table-tag-modal"
      title="编辑标签"
    >
      <div class="tag-block">
        <gs-popover
          ref="tagPop"
          trigger="click"
          placement="top"
          popper-class="add-tag-pop"
          supernatant
          @hide="handleTagPop"
        >
          <gs-form
            ref="tagForm"
            :model="tagForm"
            :rules="rules"
            label-width="50px"
          >
            <gs-form-item
              prop="name"
              label="名称"
            >
              <gs-input
                v-model="tagForm.name"
                placeholder="请输入标签名"
              />
            </gs-form-item>
            <gs-form-item class="btn-item">
              <gs-button
                type="primary"
                size="small"
                @click="addTag"
              >确定</gs-button>
              <gs-button
                type="default"
                size="small"
                @click="handleTagPop"
              >取消</gs-button>
            </gs-form-item>
          </gs-form>
          <gs-button
            slot="reference"
            class="add-tag-btn"
            type="primary"
          >新增标签</gs-button>
        </gs-popover>
        <gs-modal-select
          v-model="selectedTags"
          :loading="loadingTags"
          class="tag-select"
          max-height="180px"
          flatten
        >
          <gs-option
            v-for="item in tags"
            :key="item.tagId"
            :label="item.tagName"
            :value="item.tagId"
          />
        </gs-modal-select>
      </div>
      <template slot="footer">
        <gs-button
          :loading="isCreating"
          type="primary"
          @click="handleConfirm"
        >确定</gs-button>
        <gs-button
          :disabled="isCreating"
          @click="close"
        >取消</gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import DataQuality from './data-quality/data-quality.vue';
import TableInfo from './table-info/table-info.vue';
import FieldInfo from './field-info/field-info.vue';
import Overview from './overview/overview.vue';
import Relationship from './relationship/relationship.vue';
import Statistics from './statistics/statistics.vue';
import TableSql from './table-sql/table-sql.vue';
import * as tableInfoService from 'src/service/metadata-management/table-info';
import * as cacheService from 'src/service/cache';
import BreadCrumb from 'src/components/breadcrumb';
import './style';
export default {
  components: {
    DataQuality,
    TableInfo,
    FieldInfo,
    Overview,
    Relationship,
    Statistics,
    TableSql,
    BreadCrumb
  },

  data() {
    return {
      isLoading: false,
      activeName: 'table',
      tableInfo: {},

      visible: false,
      loadingTags: false,
      isCreating: false,
      tags: [],
      selectedTags: [],
      tagForm: {
        name: ''
      },
      rules: {
        name: [
          {
            trigger: 'blur',
            message: '请输入标签名',
            required: true
          }
        ]
      },
      readAuth: true
    };
  },
  computed: {
    isHdfsPath() {
      return this.$store.state.relation.isHdfsPath;
    },
    isFromTechnical() {
      const query = this.$route.query || {};
      return query.from === 'technical';
    }
  },
  watch: {
    activeName(value, oldvalue) {
      this.$router.replace({
        query: {
          module: value
        }
      });
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.activeName = to.query.module;
    next();
  },
  beforeMount() {
    const module = this.$route.query.module;
    if (module) {
      this.activeName = module;
    };
  },
  /**
   * 路由离开触发
   */
  beforeRouteLeave(to, from, next) {
    // 判断点击返回元数据列表时，进入缓存界面
    if ((to.path === '/main/metadata' || to.path === '/main/data') && to.query.back === null) {
      to.meta.keepAlive = true;
    }
    next();
  },
  mounted() {
    this.$store.dispatch('relation/updateHdfsPath', false);
    this.getTableInfo();
  },
  methods: {
    /**
     * 刷新表详情
     */
    refreshTableInfo() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      cacheService.refreshDataCache().then((res) => {
        this.getTableInfo();
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 查询表详情列表
     */
    getTableInfo() {
      this.isLoading = true;
      tableInfoService.getTableInfo(this.$route.params.id).then((res) => {
        this.isLoading = false;
        this.tableInfo = res;
        this.tableInfo.showTableName = `${this.tableInfo.tableName}${this.tableInfo.virtualTable ? '(虚拟表)' : ''}`;
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    showModal() {
      this.visible = true;
      this.selectedTags = this.tableInfo.tags.map(item => { return item.tagId; });
      this.getTags();
    },
    close() {
      this.visible = false;
    },
    getTags() {
      this.loadingTags = true;
      tableInfoService.getTags({query: '', limit: -1}).then(res => {
        this.tags = res || [];
        this.loadingTags = false;
      }).catch((res) => {
        this.loadingTags = false;
      });
    },
    handleTagPop() {
      this.$refs.tagForm.resetFields();
      this.$refs.tagPop.doClose();
    },
    addTag() {
      this.$refs.tagForm.validate((valid) => {
        if (valid) {
          tableInfoService.addTag({tagName: this.tagForm.name}).then(res => {
            this.$Message.success('添加标签成功');
            this.$refs.tagPop.doClose();
            this.getTags();
          });
        }
      });
    },
    handleConfirm() {
      this.isCreating = true;
      const {tableId, tableName, databaseName, status, createTime} = this.tableInfo;
      tableInfoService.addTableTag({
        tags: this.selectedTags,
        table: {
          tableId,
          tableName,
          databaseName,
          status,
          createTime
        }
      }).then(res => {
        this.isCreating = false;
        this.close();
        this.$Message.success('标签编辑成功');
        this.getTableInfo();
      }).catch(res => {
        this.isCreating = false;
      });
    }
  }
};
</script>




