<template>
  <div class="tabBody">
    <gs-row>
      <gs-radio
        v-model="type"
        label="every"
      >每月</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        label="increment"
      >
        从第 <el-input-number
          v-model="start"
          :min="0"
          :max="12"
          size="mini"
        /> 月开始，
        每隔 <el-input-number
          v-model="increment"
          :min="0"
          :max="12"
          size="mini"
        /> 月执行
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        class="long"
        label="specific"
      >具体月数(可多选)
        <el-select
          v-model="specific"
          size="mini"
          multiple
          popper-class="crontab-month-select"
        >
          <el-option
            v-for="(val, index) in 12"
            :key="index"
            :label="val"
            :value="val"
          />
        </el-select>
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>
import crontabMixin from './mixins/cron.mixin.js';

export default {
  name: 'CrontabMonth',
  mixins: [crontabMixin]
};
</script>



