<template>
  <div class="create-rule-table">
    <el-table
      :data="dataSources"
      border
    >
      <el-table-column
        align="center"
        label="字段名称"
        width="120"
      >
        <template slot-scope="scope">
          <span v-if="isView">{{ scope.row.ruleColumnName }}</span>
          <gs-select
            v-else
            v-model="scope.row.selectColumn.ruleColumnName"
            :validate-event="false"
            @change="handleColumnChange(scope)"
          >
            <gs-option
              v-for="(item, index) in columnRulesMap"
              :key="index"
              :label="item.ruleColumnName"
              :value="item.ruleColumnName"
              :title="item.ruleColumnName"
            />
          </gs-select>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="description"
        width="90"
        show-overflow-tooltip
        label="字段类型"
      >
        <template slot-scope="scope">
          <span>
            {{ scope.row.selectColumn.ruleColumnType }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        prop="ruleColumnName"
        align="center"
        label="规则名称"
      >
        <template slot-scope="scope">
          <div class="flex">
            <span v-if="isView">{{ scope.row.selectRule.ruleName }}</span>
            <gs-select
              v-else
              v-model="scope.row.selectRule"
              :validate-event="false"
              @change="computedRulesSelect(scope)"
            >
              <gs-option
                v-for="(item, index) in scope.row.selectColumn.columnRules"
                :key="index"
                :label="item.ruleName"
                :title="item.ruleName"
                :value="item"
              />
            </gs-select>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="description"
        show-overflow-tooltip
        label="说明"
      >
        <template slot-scope="scope">
          <span>
            {{ scope.row.selectRule.ruleInfo }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="type"
        width="110"
        label="阈值校验类型"
      >
        <template slot-scope="scope">
          <span v-if="isView">{{ scope.row.ruleCheckType === 0 ? '固定值' : '波动值' }}</span>
          <gs-select
            v-else
            v-model="scope.row.ruleCheckType"
            :validate-event="false"
            @change="handleCheckTypeChange(scope)"
          >
            <gs-option
              v-for="(item, index) in scope.row.selectRule.ruleCheckTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </gs-select>
        </template>
      </el-table-column>
      <el-table-column
        label="校验表达式"
        align="center"
        width="120"
        prop="type"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span v-if="isView">{{ getExpression(scope) }}</span>
          <gs-select
            v-if="scope.row.ruleCheckType === 0 && !isView"
            v-model="scope.row.ruleCheckExpression"
            :validate-event="false"
          >
            <gs-option
              v-for="(item, index) in ruleCheckExpressionOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </gs-select>
          <div v-else />
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="description"
        label="阈值大小"
        width="260"
      >
        <template slot-scope="scope">
          <div class="rule-check-content">
            <template v-if="scope.row.ruleCheckType === 1">
              <gs-popover
                :width="260"
                trigger="click"
                placement="top"
                popper-class="slider-popper"
              >
                <template slot="reference">
                  <div class="rule-check-wrap">
                    <div class="rule-check-item">
                      <span v-if="isView">{{ scope.row.ruleCheckThreshold[0] }}</span>
                      <gs-input
                        v-else
                        :disabled="isView"
                        v-model.number="scope.row.ruleCheckThreshold[0]"
                        :validate-event="false"
                        :placeholder="'橙色阈值'"
                        readonly
                        step="1"
                      />
                      <span class="threshold-unit">{{ scope.row.selectRule.ruleCheckThresholdUnit }}</span>
                    </div>
                    <div class="rule-check-item">
                      <span v-if="isView">{{ scope.row.ruleCheckThreshold[1] }}</span>
                      <gs-input
                        v-else
                        v-model.number="scope.row.ruleCheckThreshold[1]"
                        :validate-event="false"
                        :placeholder="'红色阈值'"
                        readonly
                        step="1"
                      />
                      <span class="threshold-unit">{{ scope.row.selectRule.ruleCheckThresholdUnit }}</span>
                    </div>
                  </div>
                </template>
                <gs-slider
                  v-model="scope.row.ruleCheckThreshold"
                  :disabled="isView"
                  range
                >
                  <div slot="prepend">
                    <div
                      :style="{'width': 'calc(' + scope.row.ruleCheckThreshold[0] + '% - 7px)'}"
                      class="prepend-background"
                    />
                  </div>
                  <div slot="append">
                    <div
                      :style="{'width': 'calc(' + (100 - scope.row.ruleCheckThreshold[1]) + '% - 7px)'}"
                      class="append-background"
                    />
                  </div>
                </gs-slider>
              </gs-popover>
            </template>
            <template v-else>
              <div class="rule-check-item">
                <span v-if="isView">{{ scope.row.ruleCheckThreshold[0] }}</span>
                <gs-input-number
                  v-else
                  v-model="scope.row.ruleCheckThreshold[0]"
                  :validate-event="false"
                  :placeholder="'期望值'"
                  :precision="getPrecision(scope)"
                  step="1"
                />
                <span class="threshold-unit">{{ scope.row.selectRule.ruleCheckThresholdUnit }}</span>
              </div>
            </template>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        v-if="!isView"
        align="center"
        prop="id"
        width="100"
        label="操作"
      >
        <template slot-scope="scope">
          <action-comfirm
            title="确定删除该字段吗？"
            @comfirm="deleteField(scope)"
          >
            <span
              slot="reference"
              class="remove-icon"
            >
              <gs-icon name="close" />删除
            </span>
          </action-comfirm>
        </template>
      </el-table-column>
    </el-table>

    <span
      v-if="!isView && computedColumnsRules && computedColumnsRules.length"
      class="add-icon"
      @click="addField"
    >
      <gs-icon name="plus-circle" /><span>添加</span>
    </span>
  </div>
</template>

<script>
import emitter from '@gs-ui/gs-ui/lib/_utils/mixins/emitter';
import ActionComfirm from 'src/components/action-comfirm';

export default {
  components: {
    ActionComfirm
  },

  mixins: [emitter],

  props: {
    value: {
      type: Array,
      default() {
        return [];
      }
    },
    columnsRules: {
      type: Array,
      default() {
        return [];
      }
    },
    isView: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      dataSources: this.value,
      ruleCheckExpressionOptions: [
        {
          label: '大于',
          value: 2
        },
        {
          label: '小于',
          value: 4
        },
        {
          label: '等于',
          value: 0
        },
        {
          label: '不等于',
          value: 1
        },
        {
          label: '大于等于',
          value: 3
        },
        {
          label: '小于等于',
          value: 5
        }
      ]
    };
  },
  computed: {
    computedColumnsRules() {
      const choose = this.dataSources.map(i => i.selectColumn.ruleColumnName + i.selectRule.ruleId);
      const columnRulesMap = JSON.parse(JSON.stringify(this.columnRulesMap));
      const arr = columnRulesMap.filter(item => {
        item.columnRules = item.columnRules.filter(c => !choose.includes(c.id));
        return item.columnRules && item.columnRules.length;
      });
      return arr;
    },
    columnRulesMap() {
      return this.columnsRules.map(i => {
        i.columnRules.map(r => {
          r.id = i.ruleColumnName + r.ruleId;
          return r;
        });
        return i;
      });
    }
  },

  watch: {
    value(val) {
      this.dataSources = val;
    }
  },

  methods: {
    getPrecision(scope) {
      if (['行', '个', '字节'].includes(scope.row.selectRule.ruleCheckThresholdUnit)) {
        return 0;
      } else {
        return 2;
      }
    },

    isValid(value) {
      const re = /^[a-zA-Z$_][a-zA-Z\d_]*$/;

      return value && re.test(value);
    },

    addField() {
      this.dataSources.push({
        selectColumn: this.computedColumnsRules[0],
        selectRule: this.computedColumnsRules[0].columnRules[0],
        ruleCheckType: this.computedColumnsRules[0].columnRules[0].ruleCheckTypeOptions[0].value,
        ruleCheckExpression: 2,
        ruleCheckThreshold: this.computedColumnsRules[0].columnRules[0].ruleCheckThreshold
          ? [...this.computedColumnsRules[0].columnRules[0].ruleCheckThreshold]
          : (this.computedColumnsRules[0].columnRules[0].ruleCheckTypeOptions[0].value === 1 ? [30, 60] : [0])
      });
      this.computedRulesSelect();
      this.$emit('input', this.dataSources);
    },

    computedRulesSelect() {
      const ids = this.dataSources.map(item => item.selectRule.id);
      this.dataSources = this.dataSources.map(item => {
        const map = this.columnRulesMap.find(i => i.ruleColumnName === item.selectColumn.ruleColumnName);
        if (map) item.selectColumn.columnRules = [...map.columnRules.filter(i => !ids.includes(i.id)), ...item.selectRule];
        return item;
      });
    },

    deleteField(scope) {
      this.$delete(this.dataSources, scope.$index);
      this.computedRulesSelect();
      this.$emit('input', this.dataSources);
      this.dispatch('GsFormItem', 'gs.form.change', this.dataSources);
    },

    handleColumnChange(scope) {
      const {
        $index,
        row
      } = scope;
      this.$nextTick(() => {
        const temp = this.columnRulesMap.find(item => item.ruleColumnName === scope.row.selectColumn.ruleColumnName);
        row.selectColumn = JSON.parse(JSON.stringify(temp));
        row.selectRule = row.selectColumn.columnRules[0];
        row.ruleCheckType = row.selectRule.ruleCheckTypeOptions[0].value;
        row.ruleCheckThreshold = row.selectRule.ruleCheckThreshold
          ? [...row.selectRule.ruleCheckThreshold]
          : (row.ruleCheckType === 1 ? [30, 60] : [0]);
        this.dataSources[$index] = row;
        this.computedRulesSelect();
        this.$emit('input', this.dataSources);
      });
    },
    handleCheckTypeChange(scope) {
      const {
        $index,
        row
      } = scope;
      if (row.ruleCheckType === 1) {
        // 波动值
        this.$delete(row, 'ruleCheckExpression');
        row.ruleCheckThreshold = [30, 60];
      } else {
        row.ruleCheckThreshold = [0];
        this.$set(row, 'ruleCheckExpression', 2);
      }
      this.dataSources[$index] = row;
    },
    getExpression(scope) {
      const expression = this.ruleCheckExpressionOptions.find(item => item.value === scope.row.ruleCheckExpression);
      return expression.label || '';
    }
  }
};
</script>




