<template>
  <div
    v-loading="isLoading"
    class="template-detail"
  >
    <el-table
      :data="detail"
      stripe
    >
      <el-table-column
        :width="'96'"
      >
        <template slot-scope="scope">
          <div class="switch">
            <gs-switch
              v-if="templateInfo.buildType === 0"
              :value="scope.row.status"
              @change="changeTemplateStatus"
            >
              <span slot="on">开</span>
              <span slot="off">关</span>
            </gs-switch>
            <i
              v-if="templateInfo.buildType !== 0 && !scope.row.status"
              class="iconfont icon-kaishi"
              @click="changeTemplateStatus"
            />
            <i
              v-if="templateInfo.buildType !== 0 && scope.row.status"
              class="iconfont icon-tingzhi"
              @click="changeTemplateStatus"
            />
            <gs-progress
              v-if="errorLog === '无'"
              :percent="percent"
              :size="30"
              type="circle"
            />
            <i
              v-else
              class="gs-icon-exclamation-circle"
            />
          </div>
        </template>
      </el-table-column>
      <el-table-column
        v-for="item in tableConfig"
        :key="item.name"
        :label="item.name"
        align="center"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{ scope.row[item.propertyName] }}
        </template>
      </el-table-column>
      <el-table-column
        label="错误日志"
        align="center"
        width="80"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <!-- <gs-button
            type="text-primary"
            @click="openCheckLogModal"
          >查看</gs-button> -->
          {{ errorLog }}
        </template>
      </el-table-column>
      <el-table-column
        label="启动时间"
        align="center"
        width="120"
        prop="startTime"
        show-overflow-tooltip
      />
      <el-table-column
        fixed="right"
        align="center"
        prop="user"
        label="操作"
        width="140"
      >
        <template slot-scope="scope">
          <div class="operation">
            <gs-button
              :disabled="scope.row.templateStatus === (1 || 2)"
              type="text-primary"
              @click="editTemplate(scope)"
            >编辑</gs-button>
            <gs-button
              type="text-primary"
              @click="viewTemplate(scope)"
            >查看</gs-button>
            <action-comfirm
              title="确定删除该模板吗？"
              @comfirm="deleteTemplate()"
            >
              <gs-button
                slot="reference"
                type="text-primary"
              >删除</gs-button>
            </action-comfirm>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import './template-detail.scss';
import ActionComfirm from 'src/components/action-comfirm';
import * as dataQualityService from 'src/service/metadata-management/data-quality';
export default {
  components: {
    ActionComfirm
  },
  props: {
    templateInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      isLoading: false,
      detail: [],
      percent: 0,
      tableConfig: [
        {
          name: '规则名称',
          propertyName: 'templateName'
        },
        {
          name: '生成周期',
          propertyName: 'buildTypeName'
        },
        {
          name: '表规则数',
          propertyName: 'tableRulesNum'
        },
        {
          name: '字段规则数',
          propertyName: 'columnRulesNum'
        },
        {
          name: '橙色警告数',
          propertyName: 'orangeAlerts'
        },
        {
          name: '红色警告数',
          propertyName: 'redAlerts'
        }
      ],
      interval: null,
      errorLog: '无'
    };
  },

  // beforeMount() {
  //   this.getTemplateDetail();
  // },
  watch: {
    templateInfo: {
      handler: function (val) {
        if (Object.keys(val).length) {
          this.getTemplateDetail();
          const buildTypeName = this.templateInfo.buildType === 0 ? '周期生成' : '一次性生成';
          const status = this.templateInfo.templateStatus === 1 || this.templateInfo.templateStatus === 2;
          this.detail = [{...this.templateInfo, buildTypeName, status}];
          if (this.detail[0].status) {
            this.polling();
          } else {
            clearTimeout(this.interval);
            this.interval = null;
          }
        } else {
          this.detail = [];
          clearTimeout(this.interval);
          this.interval = null;
        }
      },
      immediate: true
    }
  },
  beforeDestroy() {
    clearTimeout(this.interval);
    this.interval = null;
  },
  unactivated() {
    clearTimeout(this.interval);
    this.interval = null;
  },
  methods: {
    copyTemplate(scope) {
      this.$emit('copyTemplate', scope.row.buildType);
    },
    editTemplate(scope) {
      this.$emit('editTemplate', scope.row.buildType);
    },
    viewTemplate(scope) {
      this.$emit('viewTemplate', scope.row.buildType);
    },
    /**
     * 删除模板
     */
    deleteTemplate() {
      this.isLoading = true;
      dataQualityService.deleteTemplate(this.templateInfo.templateId).then(res => {
        this.isLoading = false;
        this.$emit('handleDelete');
        this.$Message.success('删除模板成功', 1.5);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 修改模板状态
     */
    changeTemplateStatus() {
      if (this.detail[0].status) {
        this.isLoading = true;
        dataQualityService.closeTemplate(this.templateInfo.templateId).then(res => {
          this.isLoading = false;
          this.$Message.success(`关闭模板成功`, 1.5);
          this.$emit('handleChangeStatus');
          if (this.interval) {
            clearTimeout(this.interval);
            this.interval = null;
          }
        }).catch((res) => {
          this.isLoading = false;
        });
      } else {
        this.isLoading = true;
        dataQualityService.openTemplate(this.templateInfo.templateId, this.detail[0].templateStatus).then(res => {
          this.isLoading = false;
          this.$Message.success(`开启模板成功`, 1.5);
          if (!this.interval) {
            this.polling();
          }
          this.$emit('handleChangeStatus');
        }).catch((res) => {
          this.isLoading = false;
        });
      }
    },
    getTemplateDetail() {
      dataQualityService.getTemplatePercent(this.templateInfo.templateId).then(res => {
        this.percent = res.percent ? ~~(res.percent * 100) : 0;
        this.errorLog = res.generateTime && res.content
          ? res.generateTime + ':' + res.content
          : '无';
        const status = res.status === 1 || res.status === 2;
        this.detail[0].templateStatus = res.status;
        if (this.detail[0].status !== status) {
          this.$emit('handleChangeStatus');
        }
        this.detail[0].status = status;
      });
    },
    polling() {
      if (this.interval) {
        clearTimeout(this.interval);
      }
      this.interval = setTimeout(_ => {
        this.getTemplateDetail();
        this.polling();
      }, 8000);
    }
  }
};
</script>




