<template>
  <div
    v-loading="isLoading"
    class="metadata-management-table-info relative"
  >
    <div class="text-right">
      <gs-button
        v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED' && tableInfo.edit"
        :style="{visibility: !isEdit ? 'visible' : 'hidden'}"
        @click="edit(true)"
      >编辑</gs-button>
    </div>
    <gs-form
      ref="form"
      label-width="95px"
    >
      <h3>
        <i
          :style="{transform: packUp1 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp1=!packUp1"
        />
        <span>基础信息</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp1">
          <gs-form-item
            label="表名称："
          >
            <span>{{ tableInfo.showTableName }}</span>
          </gs-form-item>
          <gs-form-item
            label="标签："
          >
            <span v-if="tableInfo.editTag">
              <gs-tag
                v-for="(tag, index) in tableInfo.tags"
                :key="tag.tagId"
                class="table-tag"
                closable
                @close="handleDelTag(index)"
              >{{ tag.tagName }}</gs-tag>
            </span>
            <span v-else>
              <gs-tag
                v-for="tag in tableInfo.tags"
                :key="tag.tagId"
                class="table-tag"
              >{{ tag.tagName }}</gs-tag>
            </span>
            <span v-if="!tableInfo.tags || !tableInfo.tags.length">无</span>
            <!-- <gs-button
              class="add-tag"
              type="primary"
              icon="edit"
              size="small"
              v-show="tableInfo.showTableName && tableInfo.edit"
              @click="showModal"
            ></gs-button> -->
          </gs-form-item>
          <!-- <gs-form-item
            label="所属业务："
          >
            <span>{{ tableInfo.business }}</span>
          </gs-form-item> -->
          <!-- <gs-form-item
            label="表关联信息："
          >
            <div
              v-for="(relation, index) in tableInfo.relations"
              :key="index"
            >{{ relation }}</div>
          </gs-form-item> -->
          <gs-form-item
            label="创建人："
          >
            <span>{{ tableInfo.owner }}</span>
          </gs-form-item>
          <gs-form-item
            label="数据Owner："
          >
            <gs-tag
              v-for="(tag, index) in tableInfo.dataOwner"
              :key="index"
              class="table-tag"
            >{{ tag }}</gs-tag>
            <span v-if="!tableInfo.dataOwner || !tableInfo.dataOwner.length">无</span>
          </gs-form-item>
          <gs-form-item
            label="更新时间："
          >
            <span>{{ tableInfo.updateTime || '-' }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            label="类别："
          >
            <span>{{ tableInfo.category }}</span>
          </gs-form-item> -->
          <gs-form-item
            label="所属数据库："
          >
            <i
              class="iconfont icon-yuanshuju"
            />
            <span>{{ tableInfo.databaseName }}</span>
          </gs-form-item>
          <gs-form-item
            label="类型："
          >
            <span>{{ tableInfo.type }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            label="表生命周期："
          >
            <span>{{ tableInfo.tableLife }}</span>
          </gs-form-item> -->
          <gs-form-item
            label="分区表："
          >
            <span>{{ tableInfo.partitionTable ? '是' : '否' }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            label="分区生命周期："
          >
            <span>{{ tableInfo.partitionLife }}</span>
          </gs-form-item> -->
          <gs-form-item
            label="格式："
          >
            <span>{{ tableInfo.format }}</span>
          </gs-form-item>
          <gs-form-item
            label="位置："
          >
            <span>{{ tableInfo.location }}</span>
          </gs-form-item>
          <gs-form-item
            ref="description"
            label="描述："
          >
            <span v-if="!isEdit">{{ tableInfo.description || '-' }}</span>
            <gs-input
              v-else
              v-model="description"
              placeholder="请输入描述"
            />
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp2 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp2=!packUp2"
        />
        <span>源系统维度</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp2">
          <gs-form-item
            label="所属系统："
            prop="subordinateSystem"
          >
            <span>{{ tableInfo.subordinateSystem || '-' }}</span>
            <!-- <gs-input
              v-else
              v-model="subordinateSystem"
              placeholder="请输入所属系统"
            /> -->
          </gs-form-item>
          <gs-form-item
            label="所属数据库："
            prop="subordinateDatabase"
          >
            <span>{{ tableInfo.subordinateDatabase || '-' }}</span>
            <!-- <gs-input
              v-else
              v-model="subordinateSystem"
              placeholder="请输入所属数据库"
            /> -->
          </gs-form-item>
          <gs-form-item
            label="源系统管理员："
            prop="systemAdmin"
          >
            <span>{{ tableInfo.systemAdmin && tableInfo.systemAdmin.length ? tableInfo.systemAdmin.join(',') : '-' }}</span>
            <!-- <gs-input
              v-else
              v-model="subordinateSystem"
              placeholder="请输入源系统管理员"
            /> -->
          </gs-form-item>
          <gs-form-item
            label="表创建时间："
          >
            <span>{{ tableInfo.createTime || '-' }}</span>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp3 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp3=!packUp3"
        />
        <span>数仓维度</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp3">
          <gs-form-item
            label="数仓管理员："
            prop="dataWarehouseAdmin"
          >
            <span>{{ tableInfo.dataWarehouseAdmin || '-' }}</span>
          </gs-form-item>
          <gs-form-item
            label="描述："
            prop="dataWarehouseDescription"
          >
            <span>{{ tableInfo.dataWarehouseDescription || '-' }}</span>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp4 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp4=!packUp4"
        />
        <span>目录维度</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp4">
          <gs-form-item
            label="所属目录："
          >
            <span>{{ tableInfo.relations && tableInfo.relations.length ? tableInfo.relations.join(',') : '-' }}</span>
          </gs-form-item>
          <gs-form-item
            label="目录管理员："
          >
            <span>{{ tableInfo.catalogAdmin && tableInfo.catalogAdmin.length ? tableInfo.catalogAdmin.join(',') : '-' }}</span>
          </gs-form-item>
          <gs-form-item
            label="数据关联时间："
          >
            <span>{{ tableInfo.relationTime || '-' }}</span>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp5 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp5=!packUp5"
        />
        <span>业务维度</span>
      </h3>
      <gs-collapse-transition>
        <div
          v-show="!packUp5"
          class="business-dimension"
        >
          <div
            v-for="(item, index) in tableInfo.businessObjects"
            :key="index"
            class="business-item"
          >
            <div class="item-content">
              <span class="label-text">对应业务对象：</span>
              <span class="value-text">{{ item.businessObject }}</span>
            </div>
            <div class="item-content">
              <span class="label-text">所属部门：</span>
              <span class="value-text">{{ item.department }}</span>
            </div>
            <div>
              <span class="label-text">业务负责人：</span>
              <span class="value-text">{{ item.businessLeader }}</span>
            </div>
          </div>
          <span v-if="!tableInfo.businessObjects || !tableInfo.businessObjects.length">无</span>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp6 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp6=!packUp6"
        />
        <span>权限信息</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp6">
          <gs-form-item
            label="权限状态："
            prop="database"
          >
            <gs-checkbox
              v-model="canRead"
              :disabled="true"
              class="table-permission"
            >读</gs-checkbox>
            <gs-checkbox
              v-model="canWrite"
              :disabled="true"
              class="table-permission"
            >写</gs-checkbox>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
    </gs-form>
    <div
      v-show="isEdit"
      class="action-btns"
    >
      <action-comfirm
        title="确定修改吗？"
        @comfirm="updateTableInfo"
      >
        <gs-button
          slot="reference"
          type="primary"
        >确定
        </gs-button>
      </action-comfirm>
      <gs-button
        @click="edit(false)"
      >取消</gs-button>
    </div>
  </div>
</template>

<script>
import './table-info.scss';
import * as tableInfoService from 'src/service/metadata-management/table-info';
import ActionComfirm from 'src/components/action-comfirm';
export default {
  components: {
    ActionComfirm
  },
  props: {
    tableInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isLoading: false,
      isEdit: false,
      packUp1: false,
      packUp2: false,
      packUp3: false,
      packUp4: false,
      packUp5: false,
      packUp6: false,
      description: '',
      subordinateSystem: '',
      canWrite: false,
      canRead: false
    };
  },
  watch: {
    tableInfo: {
      handler: function (val) {
        this.canRead = val.tablePermission ? val.tablePermission.read : false;
        this.canWrite = val.tablePermission ? val.tablePermission.write : false;
      },
      immediate: true
    }
  },
  methods: {
    edit(flag) {
      this.isEdit = flag;
      if (flag) {
        this.description = this.tableInfo.description;
        this.$refs['description'].$el.scrollIntoView();
      }
    },
    updateTableInfo() {
      if (this.description === this.tableInfo.description) {
        this.$Message.warning('暂无需要保存数据');
        return;
      }
      this.isLoading = true;
      tableInfoService.updateTableInfo({
        guid: this.$route.params.id,
        description: this.description
      }).then((res) => {
        this.isLoading = false;
        this.edit(false);
        // 由于后端修改后，无法及时修改查询的结果，所以只能先前端修改显示了
        this.tableInfo.description = this.description;
        this.$emit('refreshTableInfo');
        // this.$emit('refreshTableInfo');
        this.$Message.success('成功修改表信息');
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    handleDelTag(i) {
      const current = this.tableInfo.tags[i] || {};
      this.$Modal.confirm({
        title: '提示',
        content: `确定删除标签${current.tagName}吗？`,
        onOk: () => {
          // const current = this.tableInfo.tags[i] || {};
          let tags = [];
          this.tableInfo.tags.map(item => {
            if (item.tagId !== current.tagId) {
              tags.push(item.tagId);
            }
          });
          this.isLoading = true;
          tableInfoService.addTableTag({
            table: {
              tableId: this.$route.params.id
            },
            tags
          }).then((res) => {
            this.isLoading = false;
            // 由于后端修改后，无法及时修改查询的结果，所以只能先前端修改显示了
            // this.$delete(this.tableInfo.tags, i);
            this.$Message.success('删除标签成功');
            this.$emit('refreshTableInfo');
          }).catch((res) => {
            this.isLoading = false;
          });
        }
      });
    }
  }
};
</script>




