<template>
  <div class="tabBody">
    <gs-row>
      <gs-radio
        v-model="type"
        label="every"
      >每天</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        label="increment"
      >
        从第 <el-input-number
          v-model="start"
          :min="1"
          :max="31"
          size="mini"
        /> 天开始，
        每隔 <el-input-number
          v-model="increment"
          :min="1"
          :max="31"
          size="mini"
        /> 天执行
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        class="long"
        label="specific"
      >具体天数(可多选)
        <el-select
          v-model="specific"
          size="mini"
          multiple
          popper-class="crontab-week-select"
        >
          <el-option
            v-for="(val, index) in 31"
            :key="index"
            :value="val"
          >{{ val }}</el-option>
        </el-select>
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>
import crontabMixin from './mixins/cron.mixin.js';

export default {
  name: 'CrontabDate',
  mixins: [crontabMixin]
};
</script>



