export default {
  props: {
    value: String
  },
  data() {
    return {
      type: 'every',
      start: 1,
      increment: 1,
      specific: []
    };
  },
  computed: {
    cron() {
      let cron = '';
      switch (this.type) {
        case 'every':
          cron = '*';
          break;
        case 'increment':
          cron = `${this.start}/${this.increment}`;
          break;
        case 'specific':
          cron = this.specific.join(',');
          break;
        case 'week':
          cron = '?';
          break;
        default:
          break;
      }
      return cron;
    }
  },
  watch: {
    cron(val) {
      this.$emit('input', val);
    },
    value(val) {
      this.resolve(val);
    }
  },
  methods: {
    resolve(cron) {
      if (cron === '*' || cron === '0') {
        this.type = 'every';
      } else if (cron.includes('/')) {
        this.type = 'increment';
        this.start = cron.split('/')[0];
        this.increment = cron.split('/')[1];
      } else {
        this.type = 'specific';
        this.specific = cron.split(',');
      }
    }
  },
  created() {
    this.resolve(this.value);
  }
};



