import http from 'src/utils/http';

export function getTableInfo(id, params) {
  return http.get(`privilegecheck/metadata/table/${id}`, {
    params: {
      privilegeType: 'table',
      privilegeGuid: id
    }
  });
}

export function updateTableInfo(param) {
  return http.post('metadata/update/table', param);
}

export function getTags(param) {
  return http.post(`metadata/tags`, param);
}

export function addTag(param) {
  return http.post(`metadata/tag`, param);
}

export function addTableTag(param) {
  return http.post(`metadata/tag/table2tab`, param);
}
export function updateTableTag(param) {
  return http.post(`metadata/tag/table2tab`, param);
}



