<template>
  <div class="tabBody">
    <gs-row>
      <gs-radio
        v-model="second.cronEvery"
        label="1"
      >每秒钟</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="second.cronEvery"
        label="2"
      >
        从第 <el-input-number
          v-model="second.incrementStart"
          :min="0"
          :max="59"
          size="mini"
        /> 秒开始，
        每隔 <el-input-number
          v-model="second.incrementIncrement"
          :min="1"
          :max="60"
          size="mini"
        /> 秒执行
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="second.cronEvery"
        class="long"
        label="3"
      >具体秒数(可多选)
        <el-select
          v-model="second.specificSpecific"
          size="mini"
          multiple
        >
          <el-option
            v-for="(val, index) in 60"
            :key="index"
            :value="val-1"
          >{{ val-1 }}</el-option>
        </el-select>
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="second.cronEvery"
        label="4"
      >周期从
        <el-input-number
          v-model="second.rangeStart"
          :min="1"
          :max="60"
          size="mini"
        />
        到
        <el-input-number
          v-model="second.rangeEnd"
          :min="0"
          :max="59"
          size="mini"
        />
        秒
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>
import crontabMixin from './mixins/cron.mixin.js';

export default {
  mixins: [crontabMixin],
  data() {
    return {
      second: {
        cronEvery: '',
        incrementStart: 3,
        incrementIncrement: 5,
        rangeStart: 3,
        rangeEnd: 5,
        specificSpecific: []
      }
    };
  },
  computed: {
    text() {
      let seconds = '';
      let cronEvery = this.second.cronEvery;
      switch (cronEvery.toString()) {
        case '1':
          seconds = '*';
          break;
        case '2':
          seconds =
            this.second.incrementStart + '/' + this.second.incrementIncrement;
          break;
        case '3':
          this.second.specificSpecific.map(val => {
            seconds += val + ',';
          });
          seconds = seconds.slice(0, -1);
          break;
        case '4':
          seconds = this.second.rangeStart + '-' + this.second.rangeEnd;
          break;
        default:
          break;
      }
      return seconds;
    }
  }
};
</script>



