<template>
  <div class="crontab">
    <gs-tabs
      v-model="activeTab"
      type="content-tab"
    >
      <!-- <gs-tab-pane label="秒" name="second">
                <crontab-second v-model="second"></crontab-second>
            </gs-tab-pane> -->

      <!-- <gs-tab-pane label="分" name="minute">
                <crontab-minute v-model="minute"></crontab-minute>
            </gs-tab-pane> -->

      <gs-tab-pane
        label="时"
        name="hour"
      >
        <crontab-hour v-model="hour" />
      </gs-tab-pane>

      <gs-tab-pane
        label="天"
        name="date"
      >
        <crontab-date v-model="date" />
      </gs-tab-pane>

      <gs-tab-pane
        label="月"
        name="month"
      >
        <crontab-month v-model="month" />
      </gs-tab-pane>

      <gs-tab-pane
        label="周"
        name="week"
      >
        <crontab-week v-model="week" />
      </gs-tab-pane>

      <!-- <gs-tab-pane label="年" name="year">
                <crontab-year v-model="year"></crontab-year>
            </gs-tab-pane> -->
    </gs-tabs>

    <div class="crontab-result">
      表达式： <span>{{ expression }}</span>
    </div>

    <div
      v-if="usePreview"
      class="crontab-preview"
    >
      <span class="crontab-preview-title">最近5次运行时间</span>
      <ul class="crontab-preview-list">
        <li
          v-for="(preview, index) in previews"
          :key="index"
          class="crontab-preview-item"
        >{{ preview }}</li>
      </ul>
    </div>
  </div>
</template>
<script>
import crontabSecond from './crontab-second.vue';
import crontabMinute from './crontab-minute.vue';
import crontabHour from './crontab-hour.vue';
import crontabDate from './crontab-date.vue';
import crontabMonth from './crontab-month.vue';
import crontabYear from './crontab-year.vue';
import crontabWeek from './crontab-week.vue';

const UNSET = '*';

export default {
  components: {
    crontabSecond,
    crontabMinute,
    crontabHour,
    crontabDate,
    crontabMonth,
    crontabYear,
    crontabWeek
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    useWeek: {
      type: Boolean,
      default: false
    },
    usePreview: {
      type: Boolean,
      default: false
    },
    active: {
      type: String,
      default: 'minute',
      validator(value) {
        return ['minute', 'hour', 'date', 'month', 'week'].includes(value);
      }
    },
    previews: {
      type: Array,
      default: () => []
    }
  },
  data() {
    const { second, minute, hour, date, month, week, year } = this.resolveCron(this.value);
    return {
      activeTab: this.active,
      second,
      minute,
      hour,
      date,
      month,
      year,
      week
    };
  },
  computed: {
    expression() {
      let { second, minute, hour, date, month, week, year } = this; // 注释了second和year

      second = '0';
      minute = '0';
      return `${second} ${minute} ${hour} ${date} ${month} ${week} ${year}`;
    }
  },
  watch: {
    expression: {
      handler(val) {
        this.$emit('input', val);
      },
      immediate: true
    },
    value(val) {
      const { second, minute, hour, date, month, week, year } = this.resolveCron(val);
      this.second = second;
      this.minute = minute;
      this.hour = hour;
      this.date = date;
      this.month = month;
      this.week = week;
      this.year = year;
    },
    active(val) {
      this.activeTab = val;
    }
  },
  methods: {
    resolveCron(cron) {
      cron = cron.split(' ');
      return {
        second: cron[0] || UNSET,
        minute: cron[1] || UNSET,
        hour: cron[2] || '0',
        date: cron[3] || UNSET,
        month: cron[4] || UNSET,
        week: cron[5] || '?',
        year: cron[6] || UNSET
      };
    },
    flag(val) {
      return val !== UNSET || val === 0;
    },
    isUnset(val) {
      return val === UNSET;
    }
  }
};
</script>
<style lang='scss'>
.crontab {
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  padding: 0 8px;

  &-result {
    padding: 16px;
    border-top: 1px solid #d9d9d9;
  }

  &-preview {
    border-top: 1px solid #d9d9d9;
    padding: 16px;

    &-list {
      padding-left: 16px;
    }
  }

  .tabBody {
    .gs-row {
      margin: 10px 0;
      .long {
        .el-select {
          max-width: 240px;
        }
      }
      .gs-input-number {
        max-width: 120px;
      }
    }
  }
}
.crontab-minute-select,
.crontab-hour-select,
.crontab-day-select,
.crontab-week-select,
.crontab-month-select {
  z-index: 1000000 !important;
}
</style>



