<template>
  <div class="tabBody">
    <gs-row>
      <gs-radio
        v-model="type"
        label="every"
      >每分钟</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        label="increment"
      >
        从第 <el-input-number
          v-model="start"
          :min="0"
          :max="59"
          size="mini"
        /> 分开始，
        每隔 <el-input-number
          v-model="increment"
          :min="1"
          :max="60"
          size="mini"
        /> 分执行
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        class="long"
        label="specific"
      >具体分钟数(可多选)
        <el-select
          v-model="specific"
          size="mini"
          multiple
          popper-class="crontab-minute-select"
        >
          <el-option
            v-for="(val, index) in 60"
            :key="index"
            :value="val-1"
          >{{ val-1 }}</el-option>
        </el-select>
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>
import crontabMixin from './mixins/cron.mixin.js';

export default {
  name: 'CrontabMin',
  mixins: [crontabMixin]
};
</script>




