<template>
  <div
    v-loading="isLoading"
    class="report-detail"
  >

    <div class="title">
      <gs-button
        type="text-primary"
        @click="backToOverview"
      >
        <i class="gs-icon-revoke" />
      </gs-button>
      <gs-button
        @click="dowmLoadTemplate()"
      >下载</gs-button>
    </div>
    <h5>表质量</h5>
    <el-table
      :data="tableData"
      stripe
    >
      <el-table-column
        label="规则名称"
        align="center"
        prop="ruleName"
        show-overflow-tooltip
      />
      <el-table-column
        label="说明"
        align="center"
        prop="ruleInfo"
        show-overflow-tooltip
      />
      <el-table-column
        label="阈值类型"
        align="center"
        width="80"
        prop="ruleCheckName"
        show-overflow-tooltip
      />
      <el-table-column
        label="校验表达式"
        align="center"
        width="80"
        prop="ruleShowCheckExpression"
        show-overflow-tooltip
      />
      <el-table-column
        label="阈值大小"
        align="center"
        width="200"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <div class="rule-check-wrap">
            <div
              v-for="(value, index) in scope.row.ruleCheckThreshold"
              :key="index"
              class="rule-check-item"
            >
              {{ value }}
              {{ scope.row.ruleCheckThresholdUnit }}
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label="结果"
        align="center"
        width="120"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <div :class="scope.row.ruleStatus">
            {{ scope.row.reportRuleValue }}
          </div>
        </template>
      </el-table-column>
    </el-table>
    <h5 class="field-quality">字段质量</h5>
    <el-table
      :data="fieldData"
      stripe
    >
      <el-table-column
        label="字段名称"
        align="center"
        prop="ruleColumnName"
        show-overflow-tooltip
      />
      <el-table-column
        label="字段类型"
        align="center"
        width="120"
        prop="ruleColumnType"
        show-overflow-tooltip
      />
      <el-table-column
        label="规则名称"
        align="center"
        prop="ruleName"
        show-overflow-tooltip
      />
      <el-table-column
        label="规则说明"
        align="center"
        prop="ruleInfo"
        show-overflow-tooltip
      />
      <el-table-column
        label="阈值类型"
        align="center"
        width="80"
        prop="ruleCheckName"
        show-overflow-tooltip
      />
      <el-table-column
        label="校验表达式"
        align="center"
        width="80"
        prop="ruleShowCheckExpression"
        show-overflow-tooltip
      />
      <el-table-column
        label="阈值"
        align="center"
        width="200"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <div class="rule-check-wrap">
            <div
              v-for="(value, index) in scope.row.ruleCheckThreshold"
              :key="index"
              class="rule-check-item"
            >
              {{ value }}
              {{ scope.row.ruleCheckThresholdUnit }}
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label="结果"
        align="center"
        width="120"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <div :class="scope.row.ruleStatus">
            {{ ruleCheckThresholdUnit !== '%' ? scope.row.reportRuleValue : (~~(scope.row.reportRuleValue * 100) + '%') }}
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import './report-detail.scss';
import * as dataQualityService from 'src/service/metadata-management/data-quality';
export default {
  props: {
    reportId: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      isLoading: false,
      tableData: [],
      fieldData: []
    };
  },

  watch: {
    reportId: {
      handler: function (val) {
        if (val) this.getReportDetail();
      },
      immediate: true
    }
  },

  methods: {
    /**
     * 下载模板
     */
    dowmLoadTemplate() {
      dataQualityService.getReportsDownloadUrl([this.reportId]).then((res) => {
        this.isLoading = false;
        window.open(res.downloadUri + '?X-SSO-FullticketId=' + this.$store.state.user.userInfo.Ticket);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 查看报告详情
     */
    getReportDetail() {
      this.isLoading = true;
      dataQualityService.getReportDetail(this.reportId).then((res) => {
        this.isLoading = false;
        this.tableData = [];
        this.fieldData = [];
        res.rules.forEach(item => {
          let ruleShowCheckExpression = '';
          if (item.ruleCheckType === 0) {
            switch (item.ruleCheckExpression) {
              case 0:
                ruleShowCheckExpression = '=';
                break;
              case 1:
                ruleShowCheckExpression = '!=';
                break;
              case 2:
                ruleShowCheckExpression = '>';
                break;
              case 3:
                ruleShowCheckExpression = '>=';
                break;
              case 4:
                ruleShowCheckExpression = '<';
                break;
              case 5:
                ruleShowCheckExpression = '<=';
                break;
            }
          }
          item.ruleShowCheckExpression = ruleShowCheckExpression;

          let ruleStatus = '';
          switch (item.reportRuleStatus) {
            case 0:
              ruleStatus = 'rule-nomal';
              break;
            case 1:
              ruleStatus = 'rule-orange';
              break;
            case 2:
              ruleStatus = 'rule-red';
              break;
          }
          item.ruleStatus = ruleStatus;
          item.ruleCheckName = item.ruleCheckType === 0 ? '固定值' : '波动值';
          item.ruleType === 0 ? this.tableData.push(item) : this.fieldData.push(item);
        });
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    backToOverview() {
      this.$emit('backToOverview');
    }
  }
};
</script>




