<template>
  <div class="tabBody">
    <gs-row>
      <gs-radio
        v-model="type"
        label="every"
      >不指定</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        class="long"
        label="specific"
      >具体星期几(可多选)
        <el-select
          v-model="specific"
          size="mini"
          multiple
          popper-class="crontab-day-select"
        >
          <el-option
            v-for="(val, index) in 7"
            :key="index"
            :label="weekLabel[val-1]"
            :value="weekValue[val-1]"
          />
        </el-select>
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>
// import crontabMixin from './mixins/cron.mixin.js';

export default {
  name: 'CrontabWeek',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      type: 'every',
      start: 1,
      increment: 1,
      specific: []
    };
  },
  computed: {
    cron() {
      let cron = '';
      switch (this.type) {
        case 'every':
          cron = '?';
          break;
        case 'increment':
          cron = `${this.start}/${this.increment}`;
          break;
        case 'specific':
          cron = this.specific.join(',');
          break;
        default:
          break;
      }
      return cron;
    },
    weekValue() {
      return ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];
    },
    weekLabel() {
      return ['天', '一', '二', '三', '四', '五', '六'].map(
        val => `星期${val}`
      );
    }
  },
  watch: {
    cron(val) {
      this.$emit('input', val);
    },
    value(val) {
      this.resolve(val);
    }
  },
  created() {
    this.resolve(this.value);
  },
  methods: {
    resolve(cron) {
      if (cron === '*' || cron === '?') {
        this.type = 'every';
      } else if (cron.includes('/')) {
        this.type = 'increment';
        this.start = cron.split('/')[0];
        this.increment = cron.split('/')[1];
      } else {
        this.type = 'specific';
        this.specific = cron.split(',');
      }
    }
  }
};
</script>
<style></style>



